                                                      <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="20" height="18"><path fill="none" d="M0 0h24v24H0z"/><path d="M4 19h16v-7h2v8a1 1 0 0 1-1 1H3a1 1 0 0 1-1-1v-8h2v7zm9-10v7h-2V9H6l6-6 6 6h-5z" fill="rgba(61,97,215,1)"/></svg>
                                                </label>
                            <input type="file" @change="readURL" id="fileup" targetID="preview_progressbarTW_img" accept="image/gif, image/jpeg, image/png" />
    
                        </div>
    
                        <textinput inputtitle="縮放大小" inputwidth="108" inputplace="100" @emittextcontent="zoomscalechange"></textinput>
                    </div>
                    <div class="functwowrap">
                        <textinput inputtitle="方法二" inputwidth="204" inputplace="Url" @emittextcontent="catchurl"></textinput>
                        <textinput inputtitle="顏色" inputwidth="108" inputplace="29275F" @emittextcontent="qrcolorchange"></textinput>
                    </div>
                    <div class="clockdownloadbtn" @click="downloadtable">
                        <span>檔案下載</span>
                    </div>
                </div>
            </div>
