<script>
import textinput from './textinput'
import domtoimage from 'dom-to-image-more';
export default {
    data() {
        return {
            qrurl: '',
            qrcolor: '',
            logoscale: '',
            currentmode: "基本流程",
            menustatus: 'none',
            cardmode:'normal'
        }
    },
    methods: {
        catchurl(val) {
            this.qrurl = val;

            var url = 'https://api.qrserver.com/v1/create-qr-code/?data=' + this.qrurl + '&color=' + this.qrcolor;
            document.querySelector('#barcode').setAttribute('src', url);
        },
        qrcolorchange(val) {
            this.qrcolor = val;
            var url = 'https://api.qrserver.com/v1/create-qr-code/?data=' + this.qrurl + '&color=' + this.qrcolor;
            document.querySelector('#barcode').setAttribute('src', url);
        },
        zoomscalechange(val) {
            let a = parseInt(val, 10)
            let scale = a / 100
            this.logoscale = scale
        },
        opendrop() {
            this.menustatus = 'block'
        },
        changemode(mode) {
            if (mode == 'normal') {
                this.cardmode = 'normal';
                this.currentmode = '基本流程'
                this.menustatus = 'none'
            } else {
                this.cardmode = 'bot';
                this.currentmode = 'Bot module'
                this.menustatus = 'none'
            }
        },
        downloadtable() {
            var node = document.getElementById('content');
            let filename = this.clinicname + '小鬧鐘立牌.jpeg';
            domtoimage.toJpeg(node, { style: { 'background-color': 'white', 'transform': 'scale(1)' } })
                .then(function(dataUrl) {
                    var link = document.createElement('a');
                    link.download = filename;
                    link.href = dataUrl;
                    link.click();


                })
                .catch(function(error) {
                    console.error('oops, something went wrong!', error);
                });
        },
        readURL(event) {
            let input = event.target;
            if (input.files && input.files[0]) {

                var imageTagID = input.getAttribute("targetID");

                var reader = new FileReader();

                reader.onload = function(e) {

                    var img = document.getElementById(imageTagID);

                    img.setAttribute("src", e.target.result)

                }

                reader.readAsDataURL(input.files[0]);

            }

        },
    },
    components: {
        textinput
    }
}
</script>

